<template>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
        append-icon="mdi-search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      
      :headers="headers"
      :items="rooms"
      :search="search"
      sort-by="isEmpty"
      :sort-desc= true
    ></v-data-table>
  </v-card>
</template>


<script>
import axios from 'axios';

export default {
    name:"Table",
     data () {
      return {
        search: '',
        headers: [
          {
            text: 'Room Number',
            align: 'left',
            sortable: false,
            value: 'room',
            
          },
          { text: 'Empty', value: 'isEmpty' ,align:"center"},
          { text: 'Empty in (mins)', value: 'emptyIn' ,align:"center"},
          { text: 'Empty for (mins)', value: 'emptyFor',align:"center" },


        ],
        rooms: []
      }
    },
    computed:{
      dayName () {
          var dayNames = ['Monday', 'Thursday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'];
          return dayNames[new Date().getDay()-1];
      },},
      
    async created (){

      this.info = await axios.get('https://floating-harbor-45237.herokuapp.com/getRooms?Day='+this.dayName)
      .then(response => (this.rooms =(JSON.parse(response['data']))))
      .catch(err=>window.console.log(err));
      
    }

    

}
</script>
